import "./index.css";
import AddTodo from "./Todo/AddTodo";
import ItemsList from "./Todo/ItemList";
import { useState } from "react";
import Context from "./context";
import ReactSwipe from "react-swipe";

function App() {
  const [todos, setTodos] = useState([
    { id: 1, completed: false, title: "Купить Хлеб" },
    { id: 2, completed: false, title: "Купить масло" },
    { id: 3, completed: false, title: "Купить молоко" },
  ]);

  const Carousel = () => {
    let reactSwipeEl;

    return (
      <div>
        <ReactSwipe
          className="carousel"
          swipeOptions={{ continuous: false }}
          ref={(el) => (reactSwipeEl = el)}
        >
          <div className="first-screen">
            {" "}
            <h1 id="todos-label">Первый экран</h1>
            <AddTodo onCreate={TodoAdd} />
            {todos.length ? (
              <ItemsList todos={todos} onToggle={toggleTodo} />
            ) : (
              <p>Добавьте текст!</p>
            )}
          </div>
          <div>
            Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do
            eiusmod tempor incididunt ut labore et dolore magna aliqua.
            Vestibulum mattis ullamcorper velit sed ullamcorper. Ut sem viverra
            aliquet eget. Faucibus turpis in eu mi bibendum neque egestas.
            Laoreet sit amet cursus sit amet. Nunc vel risus commodo viverra
            maecenas accumsan lacus vel. Viverra orci sagittis eu volutpat.
            Pellentesque sit amet porttitor eget dolor morbi non arcu. Faucibus
            interdum posuere lorem ipsum dolor sit amet consectetur. Vel quam
            elementum pulvinar etiam non quam lacus suspendisse faucibus.
          </div>
        </ReactSwipe>
        <button onClick={() => reactSwipeEl.prev()}>Previous</button>
        <button onClick={() => reactSwipeEl.next()}>Next</button>
      </div>
    );
  };

  function toggleTodo(id) {
    setTodos(
      todos.map((todo) => {
        if (todo.id === id) {
          todo.completed = !todo.completed;
        }
        return todo;
      })
    );
  }

  function TodoAdd(title) {
    setTodos(
      todos.concat([
        {
          title,
          id: Date.now(),
          completed: false,
        },
      ])
    );
  }

  function removeTodo(id) {
    setTodos(todos.filter((todo) => todo.id !== id));
  }

  return (
    <Context.Provider value={{ removeTodo: removeTodo }}>
      <section aria-labelledby="todos-label" className="App wrapper">
        <Carousel />
      </section>
    </Context.Provider>
  );
}

export default App;
